.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.connection-panel {
  display: flex;
  align-items: center;
  margin: auto;
  width: 500px;
  justify-content: space-between;
}
.connection-status {
  display: flex;
  align-items: center;
  width: 150px;
  justify-content: space-between;
}
.connection-status .status {
  width: 20px;
  height: 20px;
  border-radius: 50%;
}
.status.status-disconnected {
  background: firebrick;
}
.status.status-connected {
  background: darkgreen;
}
.relay-container {
  width: 800px;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
}
.relay-item {
  width: 50px;
  height: 60px;
  margin: 10px;
  position: relative;
}
.relay-item:not(.disabled) {
  cursor: pointer;
}
.relay-item.disabled .relay-item-overlay {
  top: 20px;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: 1;
  background: rgba(255,255,255,0.5);
}
.relay-item-spinner {
  position: absolute;
  display: flex;
  width: 100%;
  height: 100%;
  background: rgba(255,255,255, 0.8);
}
.relay-item-spinner > div {
  margin: auto;
}
.input-panel {
  width: 800px;
  margin: 20px auto;
  display: flex;
}
.input-container {
  margin: 10px 5px;
  width: 90px;
}
.input {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  margin: 10px auto;
}
.input-enabled {
  background: darkgreen;
}
.input-disabled {
  background: firebrick;
}
.loader {
  width: 100%;
  height: 100vh;
  display: flex;
}
.loader > div {
  margin: auto;
}